import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,permissions,isAdmin,allStaffs,rooms} from '../../../entry/index/routes/IndexPage';
let amountTags=[{color:'blue',value:50},{color:'cyan',value:100},{color:'green',value:200},{color:'gold',value:500},{color:'orange',value:1000}];
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('addMemberModal', ()=>{
        let formValues={roomId:rooms.length>0?rooms[0].id+'':undefined,applyTime:moment(),isVip:true,consumeNow:true};
        this.setState({visible:true,formValues});
      
    });
  	this.state={
      formValues:{},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      let {formValues} = this.state;
      if(formValues.billType=='按次'&&!formValues.remainTimes){
        document.querySelector('.form-custom-id-remainTimes .ant-input-number-input').focus();
        return message.info('按次计费必须填写会员次数');
      }
      if(formValues.isVip&&!formValues.phone)return message.info('正式会员手机号必填');
      formValues.memberType=!formValues.isVip?'临时会员':'正式会员'
      util.ajaxJson(urlPrefix+'MemberOperation?opt=insertMember', {...formValues}, res=>{
        if(res.success){
          message.success(`开户成功!`);
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    emitter.emit(`queryMemberTable`, undefined, true);
    this.setState({visible:false});
  }

  render() {
    let {visible,formValues} = this.state;
  	return (<Modal title={`会员开户`} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            开户
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            取消
         </Button>]
      }>
          <Form className="add-member-modal">
            <Form.Item className="memberName-f" label="会员名" required>
                <Input {...util.getField.call(this,'name',{rules:[{required:true,message:'必填'}]})} /> <Switch checkedChildren="正式" unCheckedChildren="临时" {...util.getField.call(this,'isVip',{mode:'checkbox',onChange:checked=>{
                  if(!checked){
                    if(!formValues.name){
                      formValues.name='临时会员-'+util.formatDate(new Date().getTime());
                      formValues.billType='按次';
                      formValues.remainTimes=1;
                    }
                  }else{
                    if(formValues.name&&formValues.name.indexOf('临时会员')==0){
                      formValues.name='';
                    }
                  }
                  if(this.isFormFiledsValid===false)util.validateFields.call(this,()=>{});
                  this.setState({formValues});
                }})} />
            </Form.Item>
            <Form.Item className="" label="手机号" required={formValues.isVip}>
                <Input {...util.getField.call(this,'phone',{rules:[{required:formValues.isVip,message:'必填'},{validator:v=>{
                if(!v)return true;
                return /^1[0-9]{10}$/.test(v);
              },message:'必须为1开头的11位手机号'}]})} />
            </Form.Item>
            <Form.Item className="amount-f" label="充值金额(元)" required>
              <InputNumber {...util.getField.call(this,'amount',{rules:[{required:true,message:'必填'}]})} min={0} precision={0}/>
              {amountTags.map(it=><Tag key={'tag'+it.value} color={it.color} onClick={()=>{
                let {formValues}=this.state;
                if(isNaN(formValues.amount))formValues.amount=0;
                formValues.amount=formValues.amount+it.value;
                this.setState({formValues},()=>util.validateFields.call(this,()=>{},['amount']));
              }}>{it.value}</Tag>)}
              {formValues.amount>0&&util.convertToChinaNum(formValues.amount)&&<span className="amount-tip">{util.convertToChinaNum(formValues.amount)}元</span>}
            </Form.Item>
            <Form.Item className="" label="消费类型" required>
              <Select {...util.getField.bind(this)('billType',{rules:[{required:true,message:'必填'}]})}>
                {[{key:'按次',text:'按次'},{key:'包月',text:'包月'},{key:'包半年',text:'包半年'},{key:'包年',text:'包年'}].map(it=>{
                  return <Select.Option key={it.key} value={it.key}>{it.text}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            {formValues.billType=='按次'&&<Form.Item className="" label="会员次数" required>
                <InputNumber {...util.getField.call(this,'remainTimes')} min={1} precision={0}/>
            </Form.Item>}
            <Form.Item className="" label="开始时间" required>
                <DatePicker {...util.getField.call(this,'applyTime')} /><Checkbox {...util.getField.call(this,'consumeNow',{mode:'checkbox',onChange:checked=>{
                }})}>立即消费</Checkbox>
            </Form.Item>
            <Form.Item className="" label="球馆" required>
              <Select {...util.getField.bind(this)('roomId',{})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;